<template>
    <div>
        <el-row>
            <el-col style="padding-top: 1%;padding-left: 2%;" :span="2"> <img style="scale: 1.2;"
                    src="../../static/logo.svg" alt="BingX API Docs" class="logo"></el-col>
            <el-col :span="17"> <el-menu :default-active="'/' + $route.path.split('/')[1]" class="el-menu-demo"
                    mode="horizontal" router>
                    <el-menu-item index="/swap">USDT本位永续合约</el-menu-item>
                    <el-menu-item index="/swapV2">USDT本位永续合约V2</el-menu-item>
                    <el-menu-item index="/spot">现货</el-menu-item>
                    <el-menu-item index="/standard">标准合约</el-menu-item>
                    <el-menu-item index="/common">账户与钱包</el-menu-item>
                </el-menu></el-col>
            <el-col :span="3" class="el-menu-demo el-menu--horizontal el-menu">
                <div style="padding-top: 4%;text-align: right;margin-right: 3%;">
                    <el-input placeholder="请输入搜索内容" prefix-icon="el-icon-search" v-model="input2">
                    </el-input>
                </div>
            </el-col>
            <el-col :span="2" style="border-bottom: solid 1px #e6e6e6;">
                <div style="padding-top: 5%;text-align: center;border-bottom: solid 1px #e6e6e6;margin-right: 3%;">

                    <el-select v-model="$store.getters.app.lang.value" placeholder="请选择" style="margin-right: 3%;">
                        <el-option v-for="item in $store.getters.app.lang.options" :key="item" :label="item" :value="item">
                        </el-option>
                    </el-select>
                </div>

            </el-col>

        </el-row>
    </div>
</template>
<script>
export default {
    data() {
        return {
        };
    },
    data() {
        return {
            options: [],
            value: '',
            input1: '',
            input2: '',
            input3: '',
            input4: ''
        }
    },
    methods: {
    }
};
</script>
<style>
.el-menu.el-menu--horizontal {
    border-bottom: none;
}
.el-menu {
    background-color: white;
}
</style>



